.price {
     .price__block {
        background-color: #F9F9F9;
        padding: 1em;
        margin-bottom: 2em;
        ul {
            padding-left: 1em;
        }
        .subTitleBlock {
            font-size: 24px;
        }
        .note {
            color: #707070;
        }
        &.price__list {
            overflow-x: scroll;
            table {
                font-weight: 600;
                .title {
                    color: #790000;
                }
            }
        }
        &.indPack {
            table {
                font-weight: 600;
            }
        }
     }
}