.midImg {
    .midImg__inner {
        position: relative;
        .midImg__img {
            display: flex;
            img:first-child {
                position: absolute;
                z-index: 1;
                filter: blur(32px);
            }
            img:nth-child(2) {
                z-index: 5;
            }
            img:last-child {
                position: absolute;
                z-index: 1;
                right: 0;
                filter: blur(32px);
            }
        }
    }
}

.miloga {
    .miloga__inner {
        .miloga__context {
            .miloga__context {
                .miloga__text {
                    display: flex;
                    justify-content: space-between;
                }

            }
        }
        .miloga__pics {
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 1em;
            img {
                border: 3px solid #FFFFFF;
                box-shadow: 1px 1px 15px 0px #696969;
                width: 590px;
                height: 340px;
            }
        }
    }
}

@media (max-width: 768px) { 
    .miloga {
        .miloga__inner {
            .miloga__pics {
                grid-template-columns: 1fr;
            }
        }
    }
}