form.regForm__main {
    background-color: #f9f9f9;
    padding: 40px;
    h2.section__title {
        font-size: 24px;
    }
    input[type="checkbox"] {
        appearance: none;
        width: 20px;
        aspect-ratio: 1;
        border: 2px solid #18161D;
        background-image: url(../img/svg/mark.svg);
        background-repeat: no-repeat;
        background-position: center center;
        &:checked {
            background-color:#790000;
            border-color: #790000;
        }
    }
    .regForm__block {
        padding-top: 2em;
        padding-bottom: 2em;
        border-bottom: 1px solid #D9D9D9;
    }
    .note {
        margin-bottom: 1em;
        p {
            margin: 0;
        }
    }
    input[type="radio"] {
        appearance: none;
        width: 20px;
        aspect-ratio: 1;
        border: 2px solid #18161D;
        border-radius: 100%;
        position: relative;
        &:checked {
            border-color: #790000;
            &::after {
                content: '';
                position: absolute;
                width: 10px;
                aspect-ratio: 1;
                background-color:#790000;
                left: 3px;
                top: 3px;
                border-radius: 100%;
            }
        }

    }
    .redMark {
        color: #D93025;
    }
    input[type="text"],  input[type="email"], input[type="tel"]{
        background-color: #FFFFFF;
        border: 1px solid #D9D9D9;
        color: #18161D;
        padding: 12px 20px;
        &::placeholder {
            color: #707070;
        }
    }
    textarea {
        border: 1px solid #D9D9D9;
    }
    .row {
        margin-bottom: 1em;
        display: flex;
        align-items: center;
        column-gap: 1em;
        input {
            flex-shrink: 0;
        }
    }
    .regForm__main {
        display: flex;
        column-gap: 2em;
        .inputEl {
            display: flex;
            flex-direction: column;
            margin-bottom: 1em;
            label {
                margin-bottom: 0.2em;
            }
            &.levelField {
                margin-bottom: 2em;
            }
        }
        .row {
            margin-bottom: 0;
        }
    }
    .regForm__block.comment {
        .column {
            display: flex;
            flex-direction: column;
        }
        label {
            margin-bottom: 0.5em;
        }
    }
    button.btn.sendReg {
        background-color: #825171;
        color: #fff;
        padding: 20px;
        font-weight: 600;
        cursor: pointer;
    }
}

@media (max-width: 768px) {
    form.regForm__main {
        padding: 20px;
        .regForm__main {
            flex-direction: column;
        }
        h2.section__title {
            font-size: 18px;
            text-align: left;
        }
    }

}